.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
}

.lol {
  margin: 50%;
}

html {
  padding: 0;
  margin: 0;
  display: flex;
}

h1 {
  color: green;
  font-size: 124px;
}

body {
  background-color: purple;
  padding: 0;
  display: inline;
  transform: translate(20px);
}

@media (min-width: 768px) {
  .container {
    max-width: 740px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1300px) {
  .container {
    max-width: 1270px;
  }
}
